(function (angular) {
	'use strict';

	angular
		.module('file-browser.file')
		.factory('file-browser.file.FileModel', FileModel);

	function FileModel() {

		function File(modelData) {
			this.name = modelData.file_name || '';
			this.type = modelData.type || '';
		}

		return File;
	}
})(angular);
